@font-face {
    font-family: "Nunito";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Nunito"),
             url("Nunito-Regular.woff2") format("woff2"),
             url("Nunito-Regular.woff") format("woff");
}

